
export const LoginModalForm = {
    props: ['canCancel'],
    template: `
            <form action="">
                <div class="modal-card" style="width: auto">
                    <header class="modal-card-head">
                        <p class="modal-card-title">登陆</p>
                        <button
                            type="button"
                            class="delete"
                            @click="$emit('close')"/>
                    </header>
                    <section class="modal-card-body">
                        <b-field>
                            <b-input
                                type="username"
                                v-model="username"
                                placeholder="用户名"
                                required>
                            </b-input>
                        </b-field>

                        <b-field>
                            <b-input
                                type="password"
                                v-model="password"
                                password-reveal
                                placeholder="密码"
                                required>
                            </b-input>
                        </b-field>
                    </section>
                    <footer class="modal-card-foot">
                    <b-button
                        label="登陆"
                        type="is-primary" 
                        @click="login"/>
                        <b-button
                            label="关闭"
                            @click="() => {this.password = '';this.username = '';this.$emit('close')}" />
                        
                    </footer>
                </div>
            </form>
        `,
    methods: {
        login() {
            if (this.password === '123456' && this.username === 'admin') {
                this.password = ''
                this.username = ''
                this.$emit('login')
                this.$emit('close')
            } else {
                this.username = '登陆信息错误， 重新输入'
            }
        }
    },
    data: function () {
        return {
            password: '',
            username: ''
        }
    }
}

export const NoticeModalForm = {
    props: ['canCancel', 'isEdit', 'notice'],
    watch: {
        notice: {
            immediate: true,
            handler: function (val) {
                this.title = val.title;
                this.text = val.text;
            }
        }
    },
    template: `
            <form action="">
                <div class="modal-card" style="width: auto">
                    <header class="modal-card-head">
                        <p class="modal-card-title">{{isEdit? '编辑':'新建'}}</p>
                        <button
                            type="button"
                            class="delete"
                            @click="$emit('close')"/>
                    </header>
                    <section class="modal-card-body">
                        <b-field label="标题">
                            <b-input
                                type="text"
                                v-model="title">
                            </b-input>
                        </b-field>

                        <b-field label="正文">
                            <b-input
                                type="textarea"
                                maxlength="20000"
                                v-model="text"
                                >
                            </b-input>
                        </b-field>
                    </section>
                    <footer class="modal-card-foot">
                    <b-button
                        label="发布"
                        type="is-primary" 
                        @click="$emit('publish',{title, text}) && $emit('close')"/>
                        <b-button
                            label="关闭"
                            @click="() => {this.title = '';this.text = '';this.$emit('close')}" />
                        
                    </footer>
                </div>
            </form>
        `,
    data: function () {
        return {
            title: '',
            text: '',
        }
    }
}