<template>
  <div class="hello">
    <header>
      <div class="notices-header"></div>
    </header>
    <div class="main-content">
      <div class="notices-content" :class="{ hide: isHide }">
        <div class="main-header">
          <span>最新公告</span>
          <input class="search-input" placeholder="搜索" v-model="searchText" />
          <b-button
            type="is-primary is-light"
            class="add-button"
            :class="{hide: !isAdmin}"
            @click="openNoticeModal()"
          >添加公告</b-button>
          <b-button
            :class="{ hide: isAdmin }"
            type="is-primary is-light"
            @click="isloginModalActive = true"
            class="admin-button"
          >管理员登陆</b-button>
          <b-modal
            v-model="isloginModalActive"
            has-modal-card
            trap-focus
            :destroy-on-hide="false"
            aria-role="dialog"
            aria-label="Example Modal"
            aria-modal
          >
            <template #default="props">
              <login-modal-form @close="props.close" @login="() => {isAdmin = true}"></login-modal-form>
            </template>
          </b-modal>
          <b-button
            :class="{ hide: !isAdmin }"
            type="is-primary is-light"
            @click="() => {isAdmin = false}"
            class="admin-button"
          >管理员退出</b-button>
        </div>

        <ul>
          <li
            class="item"
            v-for="notice in displayNotices"
            @click="openNotice(notice)"
            :key="notice.index"
          >
            <a class="left notice-title">{{notice.title}}</a>
            <b-button
              size="is-small"
              :class="{ hide: !isAdmin }"
              class="right"
              @click.stop="deleteNotice(notice, true)"
              type="is-danger is-light"
            >删除</b-button>
            <b-button
              size="is-small"
              :class="{ hide: !isAdmin }"
              class="right"
              style="margin: 0px 10px;"
              @click.stop="openNoticeModal(notice, true)"
              type="is-link is-light"
            >编辑</b-button>
            <span class="right">{{notice.time.substr(0, notice.time.length-3)}}</span>
          </li>
          <li class="item" v-if="displayNotices.length === 0">暂时无公告</li>
        </ul>

        <b-modal v-model="isNoticeModalActive">
          <template #default="props">
            <notice-modal-form
              @close="props.close"
              @publish="publish"
              :isEdit="isEdit"
              :notice="currentNoticeInModal"
            ></notice-modal-form>
          </template>
        </b-modal>
      </div>

      <div class="context" v-bind:class="{ hide: !isHide }">
        <b-button size="is-small" @click="toggle()">返回</b-button>
        <div class="title">{{context.title}}</div>
        <div class="time">{{context.time}}</div>
        <div class="text">
          <p>{{context.text}}</p>
        </div>
      </div>
    </div>

    <footer class="notices-footer">
      <p>成都商报营销策划有限公司</p>
    </footer>
  </div>
</template>

<script>
import { LoginModalForm, NoticeModalForm } from "./ModalForms";
import moment from "moment";
import axios from "axios";
export default {
  name: "NoticeBoard",
  components: {
    LoginModalForm,
    NoticeModalForm
  },
  data: function() {
    return {
      context: {
        title: "",
        time: "",
        text: ""
      },
      isHide: false,
      isAdmin: true,
      isloginModalActive: false,
      isNoticeModalActive: false,
      currentNoticeInModal: {},
      isEdit: false,
      notices: [],
      searchText: ""
    };
  },
  created() {
    axios
      .get("/notice")
      .then(response => {
        this.notices = response.data.notices;
      })
      .catch(() => {
        this.$buefy.toast.open({
          duration: 800,
          message: "获取数据失败！",
          type: "is-danger"
        });
      });
  },

  computed: {
    displayNotices: function() {
      return this.notices.filter(
        notice =>
          notice.title.indexOf(this.searchText) !== -1 ||
          notice.time.indexOf(this.searchText) !== -1
      );
    }
  },
  methods: {
    openNotice(item) {
      this.isHide = true;
      this.context = item;
    },
    toggle() {
      this.isHide = !this.isHide;
    },
    openNoticeModal(
      notice = { title: "", text: "", time: "" },
      isEdit = false
    ) {
      this.isEdit = isEdit;
      this.isNoticeModalActive = true;
      this.currentNoticeInModal = notice;
    },
    async publish(notice) {
      if (this.isEdit) {
        await this.deleteNotice(this.currentNoticeInModal);
      }
      axios
        .post("/notice", {
          ...notice,
          time: moment().format("YYYY-MM-DD HH:mm:ss")
        })
        .then(response => {
          this.$buefy.toast.open({
            duration: 800,
            message: "发布成功！",
            type: "is-success"
          });
          this.notices = response.data.notices;
        })
        .catch(() => {
          this.$buefy.toast.open({
            duration: 800,
            message: "发布失败！",
            type: "is-danger"
          });
        });
    },
    async deleteNotice(notice, fromAction = false) {
      await axios
        .delete("/notice", { data: { time: notice.time } })
        .then(response => {
          if (fromAction) {
            this.$buefy.toast.open({
              duration: 800,
              message: "删除成功！",
              type: "is-success"
            });
            this.notices = response.data.notices;
          }
        })
        .catch(() => {
          if (fromAction) {
            this.$buefy.toast.open({
              duration: 800,
              message: "删除失败！",
              type: "is-danger"
            });
          }
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.main-header {
  position: relative;
  text-align: left;
  font-size: 1.5rem;
  color: #4169e1;
  height: 60px;
  line-height: 60px;
  border: #ddd 1px solid;
  border-radius: 4px;
  margin: 10px 0;
  padding: 0 10px;
}

.search-input {
  width: 250px;
  position: relative;
  top: -3px;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  margin-left: 10px;
}

h3 {
  text-align: left;
  border-bottom: 1px solid;
}

.hello {
  width: 80%;
  position: absolute;
  left: 10%;
  height: 100%;
}

.main-content {
  height: calc(100vh - 200px);
}

.notices-content ul {
  height: calc(100vh - 285px);
  overflow-y: scroll;
  list-style-type: disc;
  border: #ddd 1px solid;
  border-radius: 4px;
  padding: 10px;
}

.notices-content {
  height: 100%;
}

.item {
  width: 100%;
  height: 40px;
  display: inline-block;
  border-bottom: #ddd 1px dashed;
  font-size: 13px;
  line-height: 40px;
}

.left {
  float: left;
}

.right {
  float: right;
}

.hide {
  display: none !important;
}

.title {
  font-size: 28px;
  font-weight: 700;
  line-height: 38px;
  color: #191919;
}

.time {
  text-align: left;
  font-size: 0.75rem;
}

.text {
  font-size: 0.9rem;
  text-indent: 20px;
}

.notices-header {
  background-image: url(../assets/logo.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100px;
  width: 100%;
  border-bottom: 1px solid;
}

.notices-header a {
  color: #fff;
}

a {
  text-decoration: underline;
}

.notices-footer {
  height: 100px;
  background-color: #494747;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: #fff;
}
.modal-close.is-large {
  display: none !important;
}
textarea {
  min-height: 20em !important;
}
.admin-button {
  position: absolute !important;
  right: 0px;
  top: 10px;
}
.add-button {
  position: absolute !important;
  right: 120px;
  top: 10px;
}
.notice-title {
  text-decoration: none;
  color: #333;
}
.notice-title:hover {
  text-decoration: underline;
}
</style>
